'use strict';

import dom from 'bower:metal/src/dom/dom';
import Tooltip from 'bower:steel-tooltip/src/Tooltip';
import TooltipMenuBase from './TooltipMenu.soy.js';

/**
 * TooltipMenu component.
 */
class TooltipMenu extends Tooltip {
	/**
	 * @inheritDoc
	 */
	constructor(opt_config) {
		super(opt_config);
	}

	/**
	 * @inheritDoc
	 */
	attached() {
		super.attached();
		this.eventHandler_.add(dom.on(document, 'click', this.handleDocClick_.bind(this)));
	}

	/**
	 * Handles document click in order to hide menu.
	 * @param {!Event} event
	 * @protected
	 */
	handleDocClick_(event) {
		if (this.element.contains(event.target)) {
			return;
		}
		this.visible = false;
	}
}

/**
 * Default tooltip elementClasses.
 * @default tooltip
 * @type {string}
 * @static
 */
TooltipMenu.ELEMENT_CLASSES_MERGED = 'tooltip-menu component';

/**
 * The tag name of the main element.
 * @type {string}
 * @static
 */
TooltipMenu.ELEMENT_TAG_NAME = 'nav';

/**
 * TooltipMenu attrbutes definition.
 * @type {!Object}
 * @static
 */
TooltipMenu.ATTRS = {
	/**
	 * Delay showing and hiding the menu (ms).
	 * @type {!Array<number>}
	 * @default [0, 0]
	 */
	delay: {
		validator: Array.isArray,
		value: [0, 0]
	},

	/**
	 * Trigger events used to bind handlers to show and hide tooltip.
	 * @type {!Array<string>}
	 * @default ['click', 'mouseout']
	 */
	triggerEvents: {
		validator: Array.isArray,
		value: ['click', 'click']
	},

	/**
	 * Items to be placed inside tooltip menu. Each item must contain at least a
	 * label key.
	 * @type {!Array<!Object>}
	 */
	content: {
		validator: Array.isArray,
		valueFn: function() {
			return [];
		}
	}
};

TooltipMenuBase.setImpl(TooltipMenu);

export default TooltipMenu;
import JQueryAdapter from 'bower:metal-jquery-adapter/src/JQueryAdapter';JQueryAdapter.register('tooltipMenu', TooltipMenu)